.App {
  text-align: left;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.grid-container {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  padding: 10px;
}

@media screen and (max-width: 991px) {
  .grid-container {
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  }
}

@media screen and (max-width: 600px) {
  .grid-container {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

.grid-item {
  padding: 20px;
  font-size: 30px;
  text-align: center;
}

section#intro h1,
h2,
h3,
h4,
h5 {
  padding-left: 32px;
}

section#intro .wrap-content h1 {
  font-size: 144px;
  line-height: 170px;
  font-weight: 700;
  letter-spacing: 2px;
  position: relative;
}

@media screen and (max-width: 991px) {
  section#intro .wrap-content h1 {
    font-size: 121px;
    line-height: 160px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1 {
    font-size: 100px;
    line-height: 135px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1 {
    font-size: 81px;
    line-height: 115px;
  }
}

@media screen and (max-width: 430px) {
  section#intro .wrap-content h1 {
    font-size: 64px;
    line-height: 93px;
  }
}

@media screen and (max-width: 380px) {
  section#intro .wrap-content h1 {
    font-size: 49px;
    line-height: 80px;
  }
}

@media screen and (max-width: 767px) {
  section#intro .wrap-content h1.small {
    font-size: 110px;
    line-height: 125px;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-content h1.small {
    font-size: 80px;
    line-height: 95px;
  }
}

@media screen and (max-width: 480px) {
  section#intro .wrap-content h1.small {
    font-size: 48px;
    line-height: 60px;
  }
}

[class~="dot-highlight"] {
  color: #FF5252 !important;
}

section#intro .wrap-description {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  left: -128px;
}
@media screen and (max-width: 1199px) {
  section#intro .wrap-description {
    bottom: -100px;
    top: inherit;
    left: 0px;
    visibility: hidden;
  }
}

@media screen and (max-width: 991px) {
  section#intro .wrap-description {
    bottom: -100px;
    top: inherit;
    left: 0px;
    visibility: hidden;
  }
}

@media screen and (min-width: 768px) and (max-width: 768px) {
  section#intro .wrap-description {
    visibility: hidden;
  }
}

@media screen and (max-width: 600px) {
  section#intro .wrap-description {
    visibility: hidden;
  }
}

@media screen and (max-width: 430px) {
  section#intro .wrap-description {
    bottom: 0px;
    visibility: visible;
  }
}

@media screen and (max-width: 380px) {
  section#intro .wrap-description {
    bottom: 25px;
    visibility: visible;
  }
}

section#intro .wrap-description p {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

@media screen and (max-width: 991px) {
  section#intro .wrap-description p {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
    padding-left: 32px;
    padding-right: 32px;
  }
}

h2,
h3 {
  font-size: 120px;
  font-weight: 700;
  margin-bottom: 50px;
  margin-left: -7px;
}

h4 {
  font-size: 64px;
}

@media screen and (max-width: 1199px) {
  h4 {
    font-size: 48px;
  }
}

@media screen and (max-width: 991px) {
  h4 {
    font-size: 40px;
  }
}

@media screen and (max-width: 600px) {
  h4 {
    font-size: 33px;
  }
}

@media screen and (max-width: 1199px) {

  h2,
  h3 {
    font-size: 64px;
  }
}

@media screen and (max-width: 991px) {

  h2,
  h3 {
    font-size: 64px;
  }
}

@media screen and (max-width: 600px) {

  h2,
  h3 {
    margin-bottom: 25px;
  }
}

@media screen and (max-width: 480px) {

  h2,
  h3 {
    font-size: 50px;
    margin-left: -2px;
  }
}

@media screen and (max-width: 380px) {

  h2,
  h3 {
    font-size: 45px;
  }
}

#wrap-social {
  position: fixed;
  top: 250px;
  right: -200px;
  z-index: 99;
}

@media screen and (max-width: 991px) {
  #wrap-social {
    right: -225px;
    top: 235px;
  }
}

@media screen and (max-width: 767px) {
  #wrap-social {
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    color: #282c34;
    text-transform: uppercase;
    font-family: "Source Code Pro";
    font-size: 18px;
    line-height: 28px;
    margin-right: 36px;
    text-decoration: none;
    position: relative;
    top: 0px;
    right: 0px;
  }
}

#wrap-social ul li {
  display: inline-block;
}

#wrap-social ul li a {
  color: white;
  font-family: "Source Code Pro", monospace;
  text-transform: uppercase;
  font-size: 18px;
  line-height: 28px;
  margin-right: 36px;
  text-decoration: none;
}

#wrap-social ul {
  -webkit-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

footer .wrap-social ul li {
  display: inline-block;
}

footer .wrap-social ul li a {
  color: #282c34;
  text-transform: uppercase;
  font-family: "Source Code Pro";
  font-size: 18px;
  line-height: 28px;
  margin-right: 36px;
  text-decoration: none;
}

@media screen and (max-width: 480px) {
  footer h2 {
    font-size: 46px;
  }
}